.container {
  display: grid;
  grid-template-columns: 1fr;

  @media(min-width: 600px) {
    grid-template-columns: 1fr 1fr;
  }

  @media(min-width: 900px) {
    grid-template-columns: 1fr 1fr 1fr;
  }
  
  &__block {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;
    color: #fff;

    @each $n, $color in $colors {
      &--#{$n} {
        background-color: $color;
      }
    }
  }

}
